<!--  -->
<template>
  <div class="my-container">
    <!-- 登陆头 -->
    <div class="header user-info" v-if="user">
      <div class="base-info">
        <div class="left">
          <van-image class="avatar" :src="userInfo.photo" round fit="cover" />
          <span class="name">{{ userInfo.name }}</span>
        </div>
        <div class="right">
          <van-button size="mini" round>编辑资料</van-button>
        </div>
      </div>
      <div class="data-stats">
        <div class="data-item">
          <span class="count">{{ userInfo.fans_count }}</span>
          <span class="text">头条</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userInfo.follow_count }}</span>
          <span class="text">关注</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userInfo.fans_count }}</span>
          <span class="text">粉丝</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userInfo.like_count }}</span>
          <span class="text">获赞</span>
        </div>
      </div>
    </div>
    <!-- 登陆头 -->
    <!-- 未登录头 -->
    <div class="header not-login" v-else>
      <div class="login-btn" @click="$router.push('/login')">
        <img src="@/assets/mobile.png" class="mobile-img" />
        <span class="text">登录/注册</span>
      </div>
    </div>
    <!-- 未登录头 -->

    <!-- 导航 -->
    <van-grid :center="true" :column-num="2" class="grid-nav" clickable>
      <van-grid-item icon="photo-o" text="文字" class="grid-item">
        <i slot="icon" class="toutiao toutiao-shoucang"></i>
        <span slot="text" class="text">收藏</span>
      </van-grid-item>
      <van-grid-item icon="photo-o" text="文字" class="grid-item">
        <i slot="icon" class="toutiao toutiao-lishi"></i>
        <span slot="text" class="text">历史</span>
      </van-grid-item>
    </van-grid>
    <!-- 宫格导航 -->
    <van-cell title="消息通知" is-link />
    <van-cell title="小智同学" is-link class="md9" />
    <van-cell
      clickable
      title="退出登录"
      class="lgout-cell"
      v-if="user"
      @click="onLogout"
    />
  </div>
</template>

<script>
import { removeItem } from '@/utils/storage'
import { getUserInfo } from '@/api/user'
import { mapState } from 'vuex'
import { Toast, Dialog } from 'vant'
export default {
  data() {
    return {
      //用户信息
      userInfo: {}
    }
  },

  components: {},

  computed: {
    ...mapState(['user'])
  },

  mounted() {},

  async created() {
    //获取
    if (this.user) {
      this.loadUserInfo()
    }
  },

  updated() {},

  methods: {
    //获取用户信息
    async loadUserInfo() {
      try {
        const res = await getUserInfo()
        this.userInfo = res.data.data
        console.log(this.userInfo)
      } catch (error) {
        Toast('获取用户信息失败,请重试')
      }
    },
    //退出登录
    onLogout() {
      console.log('退出')
      Dialog.confirm({
        // title: '标题',
        message: '是否退出'
      })
        .then(() => {
          // on confirm
          //   removeItem('TOUTIAO_USER')
          this.$store.commit('setUser', null)
          Toast.success('退出成功')
          this.$router.push('/login')
        })
        .catch(() => {
          // on cancel
        })
    }
  }
}
</script>
<style lang="less" scoped>
.my-container {
  .header {
    height: 361px;
    background-color: pink;
    background: url('~@/assets/banner.png'); //波浪线防止用@导入图片出错
    background-size: cover; //让图片内容完整填充到盒子里
  }
  .user-info {
    .base-info {
      display: flex;
      justify-content: space-between;
      align-items: center;

      height: 231px;
      //   background-color: pink;
      padding: 76px 32px 23px;
      box-sizing: border-box; //border-box 怪异盒子 宽高包括内边距
      .left {
        display: flex;
        align-items: center;
        .avatar {
          height: 132px;
          width: 132px;
          margin-right: 23px;
          border: 1px solid #fff;
        }
        .name {
          font-size: 30px;
          color: white;
        }
      }
    }
    .data-stats {
      height: 130px;
      //   background-color: #ccc;
      display: flex;
      justify-content: space-around;
      .data-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: white;
        .count {
          font-size: 36px;
        }
        .text {
          font-size: 23px;
        }
      }
    }
  }
  .lgout-cell {
    text-align: center;
    color: #d86262;
  }
  .md9 {
    margin-bottom: 9px;
  }
  .grid-nav {
    .grid-item {
      height: 141px;
    }
    .toutiao {
      font-size: 45px;
    }
    .toutiao-shoucang {
      color: #eb5253;
    }
    .toutiao-lishi {
      color: #ff9d1d;
    }
    .text {
      font-size: 28px;
    }
  }

  .not-login {
    display: flex;
    justify-content: center;
    align-content: center;
    .login-btn {
      display: flex;
      justify-content: center;
      align-content: center;
      flex-direction: column;
      text-align: center;
      .mobile-img {
        width: 132px;
        height: 132px;
        margin-bottom: 15px;
      }
      .text {
        font-size: 28px;
        color: #fff;
      }
    }
  }
}
</style>
